.App {
  text-align: center;
}

.background-animation {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1; /* Position behind content */
  pointer-events: none; /* Prevent interaction */
  overflow: hidden;
}

.star {
  position: absolute;
  background-color: #fff;
  border-radius: 50%;
  opacity: 0.5;
  animation: twinkle 3s linear infinite, drift 10s linear infinite;
}

.shooting-star {
  position: absolute;
  background-color: #fff;
  border-radius: 50%;
  width: 2px;
  height: 2px;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.5), 0 0 10px rgba(255, 255, 255, 0.5);
  animation: shoot 3s linear infinite;
}

@keyframes twinkle {
  0% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.5;
  }
}

@keyframes shoot {
  0% {
    opacity: 0;
    transform: translate(0, 0);
  }
  10% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translate(300px, 300px);
  }
}

