/* Style the background-animation container to match the body background */
.background-animation {
  background-color: inherit; /* Inherit the body's background color */
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/*Home.js*/
/* General container */
.home-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 150px);
  padding: 20px;
  max-width: 1200px;
  margin: 50px auto 0; /* Increased margin-top for visual separation */

}

.about-content {
  text-align: justify;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about-container,.projects-container, .contact-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; 
  padding: 20px; 
  max-width: 1200px;
  min-height: calc(100vh - 150px);
  margin: 100px auto 0;
}

.home-content {
  text-align: center; 
  width: 100%; /* Ensure content takes full width on smaller screens */
}

.home-heading {
  font-size: 3em; /* Increased for more prominence */
  margin-bottom: 20px; /* Adjusted margin for larger text */
  font-weight: 600;
}

.home-image {
  border-radius: 50%;
  width: 350px; /* Increased image size */
  height: 350px; /* Increased image size */
  object-fit: cover;
  margin-bottom: 25px; /* Adjusted margin for larger image */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Subtle shadow */
  transition: transform 0.3s ease; /* Smooth transition */
}

/* .home-image:hover {
  transform: scale(1.03);
} */

.home-description {
  font-size: 1.3em; /* Increased for more prominence */
  line-height: 1.6;
  max-width: 700px; /* Adjusted max width */
  margin: 0 auto 15px; /* Center description */
}

.skills-list {
  margin: 40px auto; 
  border-radius: 8px;
  color: inherit; /* Inherit color from parent */
  background-color: transparent; /* Inherit background from parent */
}

.footer{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
}
