body {
  margin: 0;
  background-color: #00008B; /* Very dark blue background */
  color: #FFFFFF; /* White text color */
  font-size: 18px; /* Increased base font size */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; /* Modern font */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: radial-gradient(circle at center, #00008B, #00007a, #000069, #000058, #000047); /* Subtle radial gradient */
  min-height: 100vh;
  background-attachment: fixed; /* Ensure the background is fixed */
}

code {
  font-family: 'Courier New', monospace;
}

