/* Navigation.css with icons */
.navigation {
  position: fixed;
  top: 0;
  padding: 1.5em 0;
  width: 100%;
  background-color: transparent;
  color: white;
  margin: 0 auto;
  z-index: 1000;  
  transition: top 0.3s; /* Add transition for smooth hiding/showing */
}
.navigation-content {
  display: flex;
  justify-content: center;
  align-items: center;  

  padding: 10px 20px;
}
.nav-links-container {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: stretch;
    
    transition: max-height 0.5s ease-in-out;
}


.nav-links {
    display: flex;
    width: 100%;
    list-style: none;
    padding: 0;
    margin: 0;
    justify-content: center;
    align-items: center;
}

.nav-links li {
    padding: 0 15px; /* Increased padding for larger icons */
}
.nav-links i {
    font-size: 30px; /* Increased icon size */

}

.hamburger {
  display: none;
  cursor: pointer;
  padding: 0 20px;
}

.hamburger div {
  width: 25px;
  height: 3px;
    border-radius: 2px;
    transition: 0.4s ease-in-out;
    background-color: white;
    margin: 6px 0;
  transition: 0.4s;
}
.nav-links a{
  text-decoration: none;
  color: white;
  font-size: 18px;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
}

.nav-links a i {
}

.nav-links a::after {
    content: attr(data-tooltip);
    position: absolute;
    bottom: -30px; /* Adjust position as needed */
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    padding: 5px 10px;
    border-radius: 4px;
    font-size: 14px;
    white-space: nowrap;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    pointer-events: none;
}

.nav-links a:hover::after {
    opacity: 1;
}

.nav-links li:hover{
  /* transform: scale(1.1); */
  color:rgb(247, 203, 8);
}
/* Add transition for smoother hover effect */
.nav-links a, .nav-links li, .nav-links i {
  transition: all 0.3s ease-in-out;
}
.fa {
  color: white;
}
/* Add responsive text size */
@media (max-width: 375px) { .nav-links i{font-size:24px;} }
@media (min-width: 376px) and (max-width: 768px) { .nav-links i{font-size:28px;} }
/* Responsive styles */
@media (max-width: 768px) {
  .hamburger {
    display: block;
    position: relative;
    z-index: 1001;

  }

  .nav-links-container {
    display: flex;
    max-height: 0;
    overflow: hidden;
  }

  .nav-links-container.open {
      max-height: 500px;
  }
  .nav-links {
      flex-direction: column;
  }

  .nav-links li {
    width: 100%;
    text-align: center;
    padding: 10px 0;
  }
  .navigation-content {
    flex-direction: column;
  }
}

.navigation.hidden {
  top: -100px; /* Hide above the viewport */
}