.projects-container {
  padding: 20px;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 columns */
  gap: 20px;
}

.project-card {
    background-color: #222831;
    padding: 20px;
    margin-bottom: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border: 1px solid #393E46;
    border-radius: 10px;
    transition: box-shadow 0.3s ease, transform 0.3s ease, opacity 0.3s ease;
    position: relative; /* For absolute positioning of description */
    overflow: hidden; /* Hide overflowing content */
    height: 150px;
    display: flex; /* Use flexbox */
    flex-direction: column; /* Stack items vertically */
    justify-content: space-between;
    color: white;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    z-index: 0;
  }
  
  .project-card:hover::after{
    z-index: 1;
    transform: scale(1.05);
  }

  .project-card:hover{
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    
    /* transform-origin: top left; */
     /* Ensure the hovered card is on top */
     height: auto;

  }

  
  .project-card h2,  .project-card p {
    margin-top: 0;
    transition: color 0.3s ease;
  }
  .project-card a {
    color: white;
    transition: color 0.3s ease;
    text-decoration: none;
  }

  .project-card:hover h2,
  .project-card:hover a, .project-card:hover h2 {
    color: rgb(247, 203, 8);
  }
  
  .project-card .description {
    margin-top: 20px;
    position: relative;
    opacity: 0;
    transition: opacity 0.3s ease; /* Smooth transitions */
    visibility: hidden;
  }
  
  .project-card:hover .description {
    
    opacity: 1;
    transform: translateY(0);
    visibility: visible;
    height:auto;
  }

.project-card ul, .project-card li {
  padding: 0;
  margin: 0;
}